<template>
  <div>
    <div class="mine">
      <mHeader sidebar="ture"  head-title="我的订单"></mHeader>
      <div class="mineContent">
      <!--<van-tabs :active="active">
        <van-tab>
          <div slot="title">订单列表</div>
          <div class="mineOrder">
            <ul>
              <li>生日派对</li>
              <li>2018-10-24</li>
              <li>未完成</li>
            </ul>
          </div>
        </van-tab>
        <van-tab>
          <div slot="title">注册</div>
        </van-tab>
      </van-tabs>-->
      <div class="mineOrder">
        <div class="displayContent" v-if="displayContent" >
          没有数据
        </div>
        <ul>
          <router-link v-for="(item,index) in orderList" :to="{name:'listDetail',params:{bid:item.order_sn}}"  :key="index" tag="li">
            <div>
              <h4>{{item.theme}}</h4>
              <em>{{item.create_time}}</em>
              <h4>{{item.status}}</h4>
            </div>
          </router-link>
          <transition name="router-slid" mode="out-in">
            <router-view></router-view>
          </transition>

        </ul>
      </div>
    </div>
    </div>
  </div>
</template>

<script>
  import mHeader from '../../components/header/headerTab.vue'
  export default {
    created(){
      this.achieveList();
    },
    data(){
      return{
        active:0,
        orderList:[],
        displayContent: false
      }
    },
    beforeEact(to,from,next){
      //next()
      console.log(to,from,next)
    },
    methods:{
      achieveList(){
        this.$post('/feast_v1/order/list',{
        }) .then((response) => {
          if(response.error_code==10000){
            let dataList = response.data.orderList;
            if(dataList.length>0){
              this.orderList = dataList;
            }else{
              this.displayContent = true
            }
          }else{
            this.$toast({
              duration: 1500,
              forbidClick: true, // 禁用背景点击
              message: response.message,
            });
            this.displayContent = true
          }
        })
      }
    },
    components:{
      mHeader,
//      otherLogin
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../style/mixin";
  .mineContent{
    margin-top: .88rem;
    padding-left: .3rem;
    padding-right: .3rem;
    background: rgba(49,49,49,0.2);
    height: 100%;
    .mineOrder{
      .displayContent{
        @include center;
        @include sc(.32rem, #fff);
        @include fjc
      }
      ul{
        li{
          div{
            @include fjsb;
            height: 1rem;
            border-bottom:solid 1px #333333;
            @include sc(.3rem, #fff);
            em{
              font-style: normal;
              @include sc(.28rem, #999)
            }
          }
        }
      }
    }
  }

  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }
  .fade-enter, .fade-leave-active {
    opacity: 0;
  }
  .fade-choose-enter-active, .fade-choose-leave-active {
    transition: opacity .5s;
  }
  .fade-choose-leave, .fade-choose-leave-active {
    display: none;
  }
  .fade-choose-enter, .fade-choose-leave-active {
    opacity: 0;
  }
  .router-slid-enter-active, .router-slid-leave-active {
    transition: all .4s;
  }
  .router-slid-enter, .router-slid-leave-active {
    transform: translate3d(2rem, 0, 0);
    opacity: 0;
  }
  .toggle-cart-enter-active, .toggle-cart-leave-active {
    transition: all .3s ease-out;
  }
  .toggle-cart-enter, .toggle-cart-leave-active {
    transform: translateY(100%);
  }


</style>

